etFieldDefaults.meta;\n    },\n    getType() {\n      return this.getConfig.component || this.getConfig.type;\n    },\n  },\n  mounted() {\n    // console.log(\"handle\", this.handle);\n    // console.log(\"field\", this.fieldData);\n    // console.log(\"meta\", this.getMeta);\n  },\n  provide() {\n    return {\n      storeName: \"base\",\n    };\n  },\n};\n</script>\n\n<style scoped>\n.buildamic-field + .buildamic-field {\n  margin-top: 2rem;\n}\n</style>\n"],"sourceRoot":""}]);
